import { Component, OnInit } from '@angular/core';
import { Service } from './service';
import { CarService } from './car.service';


interface Car {
  pitStop: boolean;
  pitStopCount: number;
  distance: number;
  speed: number;
  round: number;
}


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
  providers: [CarService]
})

export class AppComponent implements OnInit {
  cars: Car[];
  timerID;
  end = false;
  constructor(private service: Service, private carService: CarService) { }

  ngOnInit() {
    this.cars = this.carService.cars;

  }

  start() {
    this.end = false;
    if (this.service.gameOver) {
      this.reset();
    }
    this.carService.initialSpeed();
    clearInterval(this.timerID);
    this.timerID = setInterval(() => this.move(), 20);
  }

  stop() {
    this.end = false;
    clearInterval(this.timerID);
    this.reset();
  }


  reset() {
    this.cars = this.carService.reset();
    this.service.start();
  }

  gameOver() {
    this.end = true;
    clearInterval(this.timerID);
    this.cars.forEach(element => element.distance = 0);
  }

  move() {
    this.cars.forEach(car => {
      if (!car.pitStop) {
        car.distance += car.speed;
      }
      if (car.distance > 700) {
        this.service.calcRound();
        if (!this.service.gameOver) {
          car.round += 1;
        }
        car.distance = -100;
        if (this.service.gameOver) {
          this.gameOver();
        }
        if ((car.round !== 0) && car.round % 5 === 0) {
          this.pitStop(car);
        }
      }
    });
  }

  pitStop(car: Car) {
    if (this.service.gameOver) {
      this.gameOver();
      return;
    }
    car.pitStop = true;
    car.pitStopCount += 1;
    car.distance = 0;

    setTimeout(() => {
      car.pitStop = false;
    }, this.carService.random(4000, 1000));
  }
}
