import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root',
})

export class Service {
  totalRound = 0;
  gameOver = false;

  calcRound() {
    if (this.totalRound !== 50) {
      this.totalRound += 1;
    } else {
      this.gameOver = true;
    }
  }

  start() {
    this.gameOver = false;
    this.totalRound = 0;
  }
}
